/* Footer.css */
.footer {  
  background-color: rgba(200, 200, 200, 0.9); /* Soft grey for a gentle appearance */
  color: #333; /* Darker text color for better contrast */
  padding: 20px;
  text-align: center;
  box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.3);
}

.footer-content {  
  max-width: 1200px; /* Center content */
  margin: 0 auto;
}

.footer-links {  
  display: flex; /* Flexbox for layout */
  justify-content: space-around;
  flex-wrap: wrap; /* Wrap on small screens */
  margin-bottom: 20px;
}

.footer-section {  
  margin: 10px;
}

.footer-section h4 {  
  margin-bottom: 10px;
  color: #ffdfba; /* Soft orange for section titles */
}

.footer-section ul {  
  list-style-type: none; /* Remove default bullet points */
  padding: 0;
}

.footer-section ul li {  
  margin: 5px 0; /* Space between items */
}

.footer-section a {  
  color: #007bff; /* Blue for links */
  text-decoration: none; /* Remove underline */
}

.footer-section a:hover {  
  text-decoration: underline; /* Underline on hover */
}

.social-links {  
  margin-top: 20px;
}

.social-links h4 {  
  margin-bottom: 10px;
  color: #ffdfba; /* Soft orange for section titles */
}

.social-links ul {  
  list-style-type: none;
  padding: 0;
}

.social-links ul li {  
  margin: 5px 0;
}

.social-links a {  
  color: #007bff; /* Blue for social links */
  text-decoration: none;
}

.social-links a:hover {  
  text-decoration: underline; /* Underline on hover */
}

/*Adds some responsive design for smaller screens */
@media (max-width: 600px) {  
  .footer-links {    
    flex-direction: column; 
    align-items: center; 
  }
}
